<template>
  <div>
    <el-row style="margin-top: 20px">
      <el-col :span="14" :offset="4">
        <header class="form_header"></header>
        <el-form label-width="110px" class="form">
          <el-row class="categorySelect">
            <el-form-item label="">
              <el-select
                style="width: 100%"
                v-model="categorySelect"
                placeholder="">
                <el-option
                  v-for="item in this.categoryList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"></el-option>
              </el-select>
            </el-form-item>
          </el-row>
          <el-row class="add_category_row" :class="showAddCategory? 'showEdit': ''">
            <div class="addCategory">
              <el-form-item label="">
                <el-input v-model="newCategory.name"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="newCategory.description"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="addNewCategory"></el-button>
              </el-form-item>
            </div>
          </el-row>
          <div class="addCategoryButton" @click="addCategoryFun">
            <i class="el-icon-caret-top edit_icon" v-if="showAddCategory"></i>
            <i class="el-icon-caret-bottom edit_icon" v-else slot="icon"></i>
            <span></span>
          </div>
        </el-form>
        <header class="form_header"></header>
        <el-form label-width="110px" class="form" :rules="foodrules" :model="foodData" ref="foodData">
          <el-row class="categorySelect">
            <el-form-item label="" prop="name">
              <el-input v-model="foodData.name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="foodData.activity"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="foodData.description"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-upload
                class="avatar-uploader"
                action="http://cangdu.org:8001/v1/addimg/food"
                :show-file-list="false"
                :on-success="handleFoodSuccess"
                :before-upload="beforeAvatarUpload">
                <img v-if="foodData.image_path" :src="'http://cangdu.org:8001/img/'+foodData.image_path" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="foodData.attributes" multiple placeholder="">
                <el-option
                  v-for="item in attributes"
                  :key="item.value"
                  :label="item.label"
                  :value="item">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <template>
                <el-radio class="radio" v-model="radio" label="1"></el-radio>
                <el-radio class="radio" v-model="radio" label="2"></el-radio>
              </template>
            </el-form-item>
            <el-row v-if="radio == 1">
              <el-form-item label="">
                <el-input-number v-model="foodData.specs[0].packing_fee" :min="0" :max="100"></el-input-number>
              </el-form-item>
              <el-form-item label="">
                <el-input-number v-model="foodData.specs[0].price" :min="0" :max="10000"></el-input-number>
              </el-form-item>
            </el-row>
            <el-row v-else style="text-align: center;margin-bottom: 20px">
              <el-button type="primary" style="margin-bottom: 10px;" @click="dialogFormVisible = true"></el-button>
              <el-table
                :data="foodData.specs"
                style="mmargin-bottom: 20px;"
                align="cneter"
                :row-class-name="tableRowClassName"
              >
                <el-table-column
                  prop="specs"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="packing_fee"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="price"
                  label="">
                </el-table-column>
                <el-table-column
                  label="">
                  <template scope="scope">
                    <el-button
                      size="small"
                      type="danger"
                      @click="handleDelete(scope.$index)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-row>
            <el-row style="text-align: center">
              <el-button type="primary" style="margin-bottom: 10px;" @click="addFood"></el-button>
            </el-row>
          </el-row>
        </el-form>
        <el-dialog title="" :visible.sync="dialogFormVisible">
          <el-form :rules="specsrules" :model="newSpecs" ref="newSpecs">
            <el-form-item label="" label-width="100px" prop="newspecs">
              <el-input v-model="newSpecs.newspecs"></el-input>
            </el-form-item>
            <el-form-item label="" label-width="100px">
              <el-input-number v-model="newSpecs.newpacking_fee" :min="0" :max="100"></el-input-number>
            </el-form-item>
            <el-form-item label=""  label-width="100px">
              <el-input-number v-model="newSpecs.newprice" :min="0" :max="10000"></el-input-number>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="addNewSpecs"> </el-button>
          </div>
        </el-dialog>
      </el-col>
    </el-row>
    <el-dialog
      title=""
      :visible.sync="hadFormID"
      size="tiny"
      v-if="hadFormID">
      <span></span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="hadFormID = false"> </el-button>
    <el-button type="primary" @click="gotoShopList"> </el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import { addNewFood, getCategory, addCategory } from '@/api/getData'
  export default {
    name: 'addFood',
    data() {
      return {
        hadFormID: false,//
        showAddCategory: false,//
        categoryList: [],//
        categorySelect: '',//index
        newCategory: {
          name: '',
          description: '',
        },//
        foodData: {
          image_path: '',//  1
          attributes: [],//   1
          specs: [{
            specs: '',
            packing_fee: 0,
            price: 20,
          }],//   1
          category_id: '',//ID    1
          restaurant_id: '',//ID   1
          name: '',//    1
          description: '',//  1
          activity: '',//  1
        },//
        attributes: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        },],//
        radio: '1',//
        dialogFormVisible: false,//
        newSpecs: {
          newspecs: '',
          newpacking_fee: 0,
          newprice: 20,
        },//
        foodrules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
          ],
        },
        specsrules: {
          newspecs: [
            { required: true, message: '', trigger: 'blur' },
          ],
        },
      }
    },
    computed: {
      ...mapState(['formID']),//ID
    },
    methods: {
      gotoShopList() {
        this.hadFormID = false;
        this.$router.push('/ShopList');
      },
      //
      addCategoryFun() {
        this.showAddCategory = !this.showAddCategory
      },
      //
      async addNewCategory() {
        Object.assign(this.newCategory, {restaurant_id: this.formID});
        const res = await addCategory(this.newCategory);
        try {
          if (res.status == 1) {
            this.$message({
              type: 'info',
              message: ''
            });
            this.newCategory = {
              name: '',
              description: '',
            };
            this.init();
            this.showAddCategory = false;
          }
        } catch (e) {

        }
      },
      // 
      handleFoodSuccess(res, file) {
        try {
          if (res.status == 1) {
            this.foodData.image_path = res.image_path;
          } else {
            this.$message.error('')
          }
        } catch (e){

        }
      },
      //
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isJPG && isLt2M;
      },
      //
      handleDelete() {
        this.foodData.specs.splice(index, 1);
      },
      //
      tableRowClassName(row,index) {
        if (index%2 === 1) {
          return 'info-row';
        }
        return '';
      },
      //
      addNewSpecs() {
        if (!this.newSpecs.newspecs) {
            this.$message({
              type: 'error',
              message: ''
            })
          return
        }
        this.dialogFormVisible = false;
        this.foodData.specs.push({
          specs: this.newSpecs.newspecs,
          packing_fee: this.newSpecs.newpacking_fee,
          price: this.newSpecs.newprice
        });
        this.newSpecs = {
          newspecs: '',
          newpacking_fee: 0,
          newprice: 20,
        }
      },
      //
      async addFood() {
        const res = await addNewFood(this.foodData);
        try {
          if (res.status == 1) {
            this.$message({
              type: 'info',
              message: ''
            })
            this.$router.push('/FoodList')
          } else {
            this.$message({
              type: 'error',
              message: res.message,
            })
          }
        } catch (e){

        }
      },
      //
      async init() {
        const res = await getCategory(this.formID);
        try {
          if (res.status == 1) {
            res.category_list.map((item, index) => {
              item.value = index;
              item.label = item.name;
            })
            this.categoryList = res.category_list;
          }
        } catch (e){

        }
      }
    },
    mounted() {
      this.init();
      this.foodData.restaurant_id = this.formID;
    },
    watch: {
      categorySelect(newValue) {
        this.foodData.category_id = this.categoryList[newValue].id;
      },
    },

    created(){
      if (!this.formID) {
          this.hadFormID = true;
      }
    },
  }
</script>

<style>
  .form{
    min-width: 400px;
    margin-bottom: 30px;
  }
  .form:hover{
     box-shadow: 0 0 8px 0 rgba(232,237,250,.6), 0 2px 4px 0 rgba(232,237,250,.5);
     border-radius: 6px;
     transition: all 400ms;
   }
  .form_header{
    text-align: center;
    margin-bottom: 10px;
  }
  .categorySelect {
    border: 1px solid #eaeefb;
    padding: 10px 30px 10px 10px;
    border-top-right-radius: 6px;
    border-top-left-radius: 6px;
  }
  .addCategory {
    background: #f9fafc;
    padding: 10px 30px 1px 10px;
    border-left: 1px solid #eaeefb;
    border-right: 1px solid #eaeefb;
  }
  .addCategoryButton {
    text-align: center;
    border: 1px solid #eaeefb;
    padding: 10px 30px 10px 10px;
    border-bottom-right-radius: 6px;
    border-bottom-left-radius: 6px;
    color: #8f9199;
    font-size: 14px;
  }
  .addCategoryButton:hover {
    color: #20a0ff;
    background: #f9fafc;
  }
  .add_category_row{
    height: 0;
    overflow: hidden;
    transition: all 400ms;
    background: #f9fafc;
  }
  .showEdit {
    height: 185px;
  }


  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 120px;
    height: 120px;
    line-height: 120px;
    text-align: center;
  }
  .avatar {
    width: 120px;
    height: 120px;
    display: block;
  }
  .el-table .info-row {
    background: #c9e5f5;
  }


  .el-table .info-row {
    background: #c9e5f5;
  }
  .cell {
    text-align: center;
  }
</style>



