<template>

  <el-row class="tac">
    <el-col :span="4" style="height: 100%">
      <el-menu :default-active="defaultActive" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" theme="dark" style="height: 100%; border-radius: 0" router>

        <el-menu-item index="manage"><i class="el-icon-menu"></i></el-menu-item>

        <el-submenu index="1">
          <template slot="title"><i class="el-icon-document"></i></template>
          <el-menu-item index="UserList"></el-menu-item>
          <el-menu-item index="ShopList"></el-menu-item>
          <el-menu-item index="FoodList"></el-menu-item>
          <el-menu-item index="OrderList"></el-menu-item>
          <el-menu-item index="AdminList"></el-menu-item>
        </el-submenu>

        <el-submenu index="2">
          <template slot="title"><i class="el-icon-plus"></i></template>
          <el-menu-item index="addShop"></el-menu-item>
          <el-menu-item index="addFood"></el-menu-item>
        </el-submenu>

        <el-submenu index="3">
          <template slot="title"><i class="el-icon-star-on"></i></template>
          <el-menu-item index="/UserDis"></el-menu-item>
        </el-submenu>

        <!--<el-submenu index="4">-->
          <!--<template slot="title"><i class="el-icon-edit"></i></template>-->
          <!--<el-menu-item index="4-1"></el-menu-item>-->
        <!--</el-submenu>-->

        <!--<el-submenu index="5">-->
          <!--<template slot="title"><i class="el-icon-setting"></i></template>-->
          <!--<el-menu-item index="/AdminSetUp"></el-menu-item>-->
        <!--</el-submenu>-->

        <el-submenu index="6">
          <template slot="title"><i class="el-icon-information"></i></template>
          <el-menu-item index="/Explain"></el-menu-item>
        </el-submenu>

      </el-menu>
    </el-col>

    <el-col :span="20" style="height: 100%;overflow: scroll">
      <el-col :span="24" style="background: #eff2f7;height: 60px">
        <head-top></head-top>
      </el-col>
      <el-col :span="24" style="background: white">
        <router-view></router-view>
      </el-col>
    </el-col>

  </el-row>
</template>

<script>
  import headTop from '../components/headTop'
  export default {
    name: 'manage',
    methods: {
      handleOpen(key, keyPath) {
      },
      handleClose(key, keyPath) {
      }
    },
    components: {
        headTop,
    },
    computed: {
      defaultActive: function(){
        return this.$route.path.replace('/', '');
      }
    },
  }
</script>

<style scoped="">
  * {
    text-align: left;
  }
  .tac {
    height: 100%;
  }
</style>



