<template>
  <div>
    <el-row style="margin-top: 20px">
      <el-col :span="12" :offset="4">
        <el-form :model="formData" :rules="rules" label-width="100px" ref="form">

          <el-form-item label="" prop="name">
            <el-input v-model="formData.name"></el-input>
          </el-form-item>

          <el-form-item label="" prop="address">
            <el-autocomplete
              v-model="formData.address"
              :fetch-suggestions="querySearchAsync"
              @select="handleSelect"
              style="width: 100%"
              :trigger-on-focus="false"
              placeholder=""
            ></el-autocomplete>
            <span>: {{city.name}}</span>
          </el-form-item>

          <el-form-item label="" prop="phone">
            <el-input v-model="formData.phone"></el-input>
          </el-form-item>

          <el-form-item label="" prop="description">
            <el-input v-model="formData.description"></el-input>
          </el-form-item>

          <el-form-item label="" prop="promotion_info">
            <el-input v-model="formData.promotion_info"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-cascader
              :options="options"
              v-model="formData.category"
              change-on-select
            ></el-cascader>
          </el-form-item>

          <el-form-item label="" style="white-space: nowrap">
            <span></span>
            <el-switch
              v-model="formData.is_premium"
              on-text=""
              off-text="">
            </el-switch>
            <span></span>
            <el-switch
              v-model="formData.delivery_mode"
              on-text=""
              off-text="">
            </el-switch>
            <span></span>
            <el-switch
              v-model="formData.new"
              on-text=""
              off-text="">
            </el-switch>
            <br>
            <span></span>
            <el-switch
              v-model="formData.bao"
              on-text=""
              off-text="">
            </el-switch>
            <span></span>
            <el-switch
              v-model="formData.zhun"
              on-text=""
              off-text="">
            </el-switch>
            <span></span>
            <el-switch
              v-model="formData.piao"
              on-text=""
              off-text="">
            </el-switch>
          </el-form-item>

          <el-form-item label="">
            <el-input-number v-model="formData.float_delivery_fee" :min="0" :max="20"></el-input-number>
          </el-form-item>

          <el-form-item label="">
            <el-input-number v-model="formData.float_minimum_order_amount" :min="0" :max="100"></el-input-number>
          </el-form-item>

          <el-form-item label="">
            <el-time-select
              v-model="formData.startTime"
              :picker-options="{
                start: '05:30',
                step: '00:15',
                end: '23:30'
              }"
              placeholder="">
            </el-time-select>

            <el-time-select
              v-model="formData.endTime"
              :picker-options="{
                start: '05:30',
                step: '00:15',
                end: '23:30'
              }"
              placeholder="">
            </el-time-select>
          </el-form-item>

          <el-form-item label="">
            <el-upload
              class="avatar-uploader"
              action="http://cangdu.org:8001/v1/addimg/shop"
              :show-file-list="false"
              :on-success="handleShopAvatarSuccess"
              :before-upload="beforeAvatarUpload">
              <img v-if="formData.image_path" :src="'http://cangdu.org:8001/img/'+formData.image_path" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>

          <el-form-item label="">
            <el-upload
              class="avatar-uploader"
              action="http://cangdu.org:8001/v1/addimg/shop"
              :show-file-list="false"
              :on-success="handleBusinessAvatarSuccess"
              :before-upload="beforeAvatarUpload">
              <img v-if="formData.business_license_image" :src="'http://cangdu.org:8001/img/'+formData.business_license_image" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>

          <el-form-item label="">
            <el-upload
              class="avatar-uploader"
              action="http://cangdu.org:8001/v1/addimg/shop"
              :show-file-list="false"
              :on-success="handleServiceAvatarSuccess"
              :before-upload="beforeAvatarUpload">
              <img v-if="formData.catering_service_license_image" :src="'http://cangdu.org:8001/img/'+formData.catering_service_license_image" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>

          <el-form-item label="">
            <el-select v-model="activityValue" @change="selectActivity" :placeholder="activityValue">
              <el-option
                v-for="item in activitiesOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-table
              :data="formData.activities"
              style="min-width: 600px;margin-bottom: 20px;"
              align="cneter"
              :row-class-name="tableRowClassName"
            >
              <el-table-column
                prop="icon_name"
                label=""
                width="120">
              </el-table-column>
              <el-table-column
                prop="name"
                label=""
                width="120">
              </el-table-column>
              <el-table-column
                prop="description"
                label="">
              </el-table-column>
              <el-table-column
                label=""
                width="120">
                <template scope="scope">
                  <el-button
                    size="small"
                    type="danger"
                    @click="handleDelete(scope.$index)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-form-item>

          <el-form-item style="text-align: center;margin-bottom: 100px">
            <el-button type="primary" @click="submitForm"></el-button>
          </el-form-item>

        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import { getGuessCity, getCategoryLists, getCitySearchLists, addNewShop } from '@/api/getData'
  export default {
    name: 'addShop',
    data() {
      return {
        formData: {
          name: '',//
          address: '',//
          phone: '',//
          float_delivery_fee: 5,//
          float_minimum_order_amount: 20,//
          category: ['', ''],//
          latitude: '',//
          longitude: '',//
          image_path: '',//

          description: '',//
          promotion_info: '',//
          is_premium: true,//,false
          delivery_mode: true,//false
          new: true,//false
          bao: true,//false
          zhun: true,//false
          piao: true,//false
          startTime: '',//
          endTime: '',//
          business_license_image: '',//
          catering_service_license_image: '',//
          activities: [{
            icon_name: '',
            name: '',
            description: '305608',
          }],//[{icon_name:'', name:'', description: ''}]
        },
        rules: {
          name: [
            { required: true, message: '' }
          ],
          address: [
            { required: true, message: '' }
          ],
          phone: [
            { required: true, message: '' }
          ],
        },
        city: {},//
        options: [],//
        activitiesOptions: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }],
        activityValue: '',//
      }
    },
    methods: {
        //
      async initData() {
        const resa = await getGuessCity(); //
        this.city = resa;

        const resb = await getCategoryLists();//
        resb.shift();//    
        resb.forEach(item => {
          if (item.sub_categories) {
            let addNew = {
              value: item.name,
              label: item.name,
              children: [],
            };
            item.sub_categories.forEach((child,index) => {
              if (index != 0) {
                addNew.children.push({
                  value: child.name,
                  label: child.name,
                })
              }
            });
            this.options.push(addNew);
          }
        });
      },
      // 
      handleSelect(address) {
          this.formData.latitude = address.latitude;
          this.formData.longitude = address.longitude;
      },
      // 
      async querySearchAsync(queryString, cb) {
        const res = await getCitySearchLists(this.city.id, queryString);

        if (res instanceof Array) {
          res.map(item => {
            item.value = item.address;
            return item;
          });
          cb(res)
        }
      },
      // 
      handleShopAvatarSuccess(res, file) {
        if (res.status == 1) {
          this.formData.image_path = res.image_path;
        } else {
          this.$message.error('')
        }
      },
      //
      handleBusinessAvatarSuccess(res, file) {
        if (res.status == 1) {
          this.formData.business_license_image = res.image_path;
        } else {
          this.$message.error('')
        }
      },
      //
      handleServiceAvatarSuccess(res, file) {
        if (res.status == 1) {
          this.formData.catering_service_license_image = res.image_path;
        } else {
            this.$message.error('')
        }
      },
      //
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isJPG && isLt2M;
      },
      //
      selectActivity() {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(({ value }) => {
          if (value == null) {
            this.$message({
              type: 'success',
              message: ''
            });
          } else {
            let newObj = {};
            switch (this.activityValue) {
              case '':
                  newObj = {
                    icon_name: '',
                    name: '',
                    description: value,
                  };
                break;
              case '':
                newObj = {
                  icon_name: '',
                  name: '',
                  description: value,
                };
                break;
              case '':
                newObj = {
                  icon_name: '',
                  name: '',
                  description: value,
                };
                break;
              case '':
                newObj = {
                  icon_name: '',
                  name: '',
                  description: value,
                };
                break;
            }
            this.formData.activities.push(newObj);
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      tableRowClassName(row,index) {
        if (index%2 === 1) {
          return 'info-row';
        }
        return '';
      },
      // 
      handleDelete(index) {
        this.formData.activities.splice(index, 1);
      },
      //
      submitForm(){
        this.$refs.form.validate(async (value) => {
          this.formData.category.join('/');
          const aaa = await addNewShop(this.formData);
          const res = await aaa.json();
          if(res.status == 1) {
            this.$message({
              type: 'info',
              message: '',
            });
            this.$router.push('/ShopList');
          }
        })

      }
    },
    mounted() {
      this.initData();
    }
  }
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 120px;
    height: 120px;
    line-height: 120px;
    text-align: center;
  }
  .avatar {
    width: 120px;
    height: 120px;
    display: block;
  }
  .el-table .info-row {
    background: #c9e5f5;
  }
</style>



